@import url(https://fonts.googleapis.com/css2?family=Asap:wght@400;600;700&family=Dancing+Script:wght@600&family=Lobster&family=Trocchi&display=swap);
header {
  height: 100vh;
  padding-top: 5rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}
.main img {
  max-width: 100%;
}
.main {
  background-image: linear-gradient(to bottom, rgba(37, 34, 34, 0.5), #020c1b),
    url(/static/media/Roche.4114d64c.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  max-width: 100%;
}

.jr {
  margin-top: 2.5rem;
}

.bottom {
  position: absolute;
  right: 0.5rem;
  bottom: 5rem;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
  font-weight: 400;
  font-size: 0.8rem;
}
@media screen and (max-width: 1024px) {
}

nav {
  position: fixed;
  top: 0;
  left: 75%;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* padding: 1.5rem; */
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  opacity: 0.6;
  background: #020c1b;
}
nav a {
  background: transparent;
  padding: 1rem;
  border-radius: 50%;
  display: flex;
  color: #00ffff;
  font-size: 1rem;
}
nav a:hover {
  background: #00ffff;
  transition: 0.7s;
}

.about {
  display: grid;
  grid-template-columns: 35% 50%;
}
h2 {
  text-align: center;
}

.imgBx {
  width: 100%;
  height: 100%;
  background: linear-gradient(25deg, transparent #00ffff);
  display: grid;
  margin-top: -4rem;
}
.imgBx img {
  border-radius: 2rem;
  width: 18rem;
  border: solid;
  border-color: #0ffff7 solid;
  overflow: hidden;
  margin-left: 100px;
  object-fit: cover;
  margin-top: 4rem;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}
.imgBx img:hover {
  z-index: 1;

  color: #5d07e9;
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  box-shadow: 0 25px 40px rgb(58, 86, 87);
  -webkit-transform: rotate(10);
          transform: rotate(10);
}
.text {
  display: grid;
  grid-template-columns: repeat(1fr, 1fr);
}
.article-a {
  border: 1px solid transparent;
  border-radius: 1rem;
  text-align: center;
  margin-top: -2rem;
  width: 10rem;
  height: 6rem;
  color: #020c1b;
  background-color: #0ffff7;
  overflow: hidden;
  font-size: 16px;
}

.article-a small {
  font-weight: 700;
}
.article-a:hover {
  background: transparent;
  border: #5d07e9 3px solid;
  color: #d6f5fa;
}

.icon {
  font-size: 0.95rem;
  margin-top: 0.2rem;
  margin-bottom: 0.5rem;
}

.decription p {
  margin-top: 2rem;
}
.decription a {
  margin-top: 1rem;
}

@media screen and (max-width: 1024px) {
  .about {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .decription p {
    margin-top: 3rem;
    width: 200px;
  }
  .imgBx img {
    width: 75%;
    margin-left: 2rem;
  }
}
@media screen and (max-width: 600px) {
  about {
    width: 65%;
    margin: 0 auto 3rem;
    grid-template-columns: 1fr 1fr;
    grid-gap: rem;
    gap: rem;
  }
}

.experience {
  display: grid;
  grid-template-columns: 1 fr 1fr;
}
.skills {
  background: #0ffff7;
  padding: 2.4rem 5rem;
  width: 20rem;
  color: #020c1b;
  border-radius: 2rem;
  border: 2px solid transparent;
  text-align: center;
  margin-left: 30rem;
}
.skills:hover {
  background: transparent;
  border-color: rgba(241, 241, 241, 0.822);
  color: #d6f5fa;
  cursor: default;
  border-color: #5d07e9;
}
.experience-info {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.experience h3 {
  text-align: center;
  margin-bottom: 2rem;
}

.experience h4 {
  margin-left: 6px;
}
.text-a {
  text-align: center;
}
@media screen and (max-width: 1024px) {
  h2 {
    margin-top: 4rem;
  }
  .skills {
    width: 20rem;
    margin: 30px;
    margin-top: 4rem;
  }
}
@media screen and (max-width: 600px) {
  h2 {
    margin-top: 10rem;
  }
  .skills {
    width: 20rem;
    margin: 30px;
    margin-top: 4rem;
  }
}

.porfolio {
  display: grid;
  grid-template-columns: (1fr 1fr);
  margin-left: 30rem;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  width: 18rem;
  height: 75%;
}

.folio-items {
  background-color: #00ffff;
  border-radius: 2rem;
  color: #020c1b;
  transition: var(--transition);
  overflow: hidden;
}
.folio-items:hover {
  background: transparent;
  color: #d6f5fa;
  cursor: default;
  border: #5d07e9 3px solid;
  transition: transition;
}
.title {
  display: flex;
  justify-content: center;
  font-size: 1.5;
}
.icons {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 0.5rem;
}
.icons a {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  border: #020c1b;
}
.btn-a {
  color: #020c1b;
  border-radius: 0.5rem;
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  padding: 0.75rem 1rem;
  cursor: pointer;
}

.container {
  width: 60%;
  display: grid;
  grid-template-columns: 30% 60%;
  grid-gap: 12rem;
  gap: 12rem;
  margin-left: 100px;
}
.contact-options {
  display: flex;
  flex-direction: column;
  color: #020c1b;
  text-decoration: none;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
.contact-option a {
  text-decoration: none;
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.9rem;
}
.contact-option {
  background-color: #0ffff7;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  overflow: hidden;
  transition: var(--transition);
}
.contact-option a {
  color: #5d07e9;
  margin-bottom: 1rem;
}

.contact-option:hover {
  background: transparent;
  border: #5d07e9 3px solid;
  color: #d6f5fa;
  transition: transition;
}
.contact-option i {
  font-size: 2rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}
form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  margin-right: 8rem;
}
.btn {
  position: relative;
  width: 12rem;
  height: 3rem;
  background: transparent;
  color: #d6f5fa;
  border-color: #00ffff;
  margin-top: 20px;
  cursor: pointer;
}
input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background-color: transparent;
  color: #fff;
  border: 2px solid;
}
@media screen and (max-width: 1024px) {
  .container {
    width: 50%;
  }
  form {
    width: 50%;
  }
}
@media screen and (max-width: 600px) {
  .container {
    width: 25%;
  }
  form {
    width: 25%;
    margin-top: 50rem;
    margin-left: 10rem;
  }
}

.footer {
  background-color: #00ffff;
  color: #020c1b;
}
.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.social a {
  color: #020c1b;
  font-size: 1.2rem;
  font-weight: 400;
  margin: 0.8rem;
  transition: (0.5s);
}
.social a :hover {
  background: transparent;
  color: #5d07e9;
  cursor: default;
  transition: trasition;
}
.copy {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}
:root {
  --transition: all 250ms ease;
  --container-width-jf: 75%;
  --container-width-rh: 86%;
  --container-width-sn: 98%;
}
body {
  background-color: #020c1b;
  color: #d6f5fa;
  font-family: "Asap", sans-serif;
  line-height: 1.7;
  flex-wrap: wrap;
}
.container {
  width: 75%;
  width: var(--container-width-jf);
}
h1 h2 h3 h4 {
  font-weight: 400;
}
/* h4 {
  color: #00ffff;
} */
h1 {
  font-size: 2.5rem;
}
section {
  margin-top: 8rem;
}
section > h2 section > h5 {
  text-align: center;
}
section > h2 {
  margin-bottom: 3rem;
}
a {
  color: #00ffff;
  transition: all 250ms ease;
  transition: var(--transition);
}
a:hover {
  color: #fff;
}
.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #d6f5fa;
  padding: 0.75rem 1rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #00ffff;
  transition: all 250ms ease;
  transition: var(--transition);
}
.btn:hover {
  background-color: rgb(58, 86, 87);
}
img {
  display: block;
  width: 100%;
  object-fit: cover;
}
@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-rh);
  }
  section {
    margin-top: 6rem;
    height: 100vh;
  }
}
@media screen and (max-width: 600px) {
  .container {
    width: 98%;
    width: var(--container-width-sn);
  }
  section h2 {
    margin-bottom: 3rem;
  }
}

